@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-grey{
  background-color: black; /* Couleur personnalisée */
}

.txt-black{
  color: white;
}

.txt-grey{
  color: white;
}

.txt-yellow{
  color : rgb(118, 104, 56);

}

.border-yellow {
  --tw-border-opacity: 1;
  border-color: rgb(118 104 56 / var(--tw-border-opacity)) /* #eab308 */;
}

.bg-button{
  background-color: #342E2E;
}

.titre{
  font-family: 'Marcellus', serif;
}

.paragraphe{
  font-family: 'PT Serif', serif;
}

.element {
  position: absolute; /* Positionnement absolu par rapport au conteneur */
  bottom: 0; /* Place l'élément en bas du conteneur */
  left: 50%; /* Centrage horizontal (facultatif) */
  transform: translateX(-50%); /* Ajuste le centering à l'élément */
}

.degrade-jaune{
    height: 600px; /* Hauteur de l'élément */
    background: linear-gradient(to bottom, rgb(66, 58, 29), rgb(21, 21, 18)); 
}

.degrade-rouge{
  height: 600px; /* Hauteur de l'élément */
  background: linear-gradient(to bottom, rgb(52, 19, 16), rgb(21, 21, 18)); 
}

.degrade-violet{
  height: 600px; /* Hauteur de l'élément */
  background: linear-gradient(to bottom, rgb(31, 9, 41), rgb(21, 21, 18)); 
}